import { GetMusicDetail } from '../../../network/music/MusicRequests'
export default {
  mounted: function () {
    this.initData()
  },
  data () {
    return {
      id: this.$attrs.data,
      musicDetail: {
        cover: '',  // 图片
        title: '', // 标题
        story_title: '', // 音乐故事图片
        story: '', // 音乐故事
        lyric: '', // 歌词
        web_url: '', // 地址
        story_summary: '', // 一句话
        author: {
          user_name: '',
          desc: ''
        }
      }
    }
  },
  methods: {
    initData: function () {
      let self = this
      let params = this.id
      new GetMusicDetail(params).start(function (data) {
        self.musicDetail = data.data.data
      })
    },
    hrefMusic: function () {
      window.location.href = this.musicDetail.web_url
    }
  }
}



